
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  background: linear-gradient(
    90deg,
    #AC7D88 0%,
    #DEB6AB 100%
  );
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #FCFFE7;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #85586F;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #DEB6AB;
  outline: none;
  width: 320px;
  background: transparent;
  color: #85586F;
}

.todo-input::placeholder {
  color: #85586F;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: #DEB6AB;
  color: #85586F;
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid #85586F;
}

.todo-button.edit {
  background: #B1BCE6;
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #85586F;
  background: #DEB6AB;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.todo-row:nth-child(2n + 1) {
  background: #DEB6AB;
}

.todo-row:nth-child(2n + 2) {
  background: #FFE59D;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}